import { DefaultApolloClient } from '@speedlo/graphql'
import { Sentry } from '@speedlo/tools'
import { setContext } from 'apollo-link-context'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'

import { appConfig } from '../config'
import { BaseModel } from './BaseModel'

export const AuthModel = BaseModel.named('Auth')
  .props({
    token: types.maybeNull(types.string),
    userId: types.maybeNull(types.string),
  })
  .views(self => ({
    get isValid() {
      return Boolean(self.token && self.token.length)
    },
  }))
  .actions(self => ({
    authenticate(token: string, userId: string) {
      self.token = token
      self.userId = userId
      Sentry.addBreadcrumb({
        category: 'Auth',
        level: Sentry.Severity.Info,
        data: {
          userId,
          token,
        },
      })
    },
  }))
  .actions(self => {
    let fetching

    const fetch = async () => {
      if (!fetching) {
        fetching = fetchToken()
        const { token, userId } = await fetching
        self.authenticate(token, String(userId))
        fetching = null
      }
      return fetching
    }

    const authLink = setContext(async () => {
      if (!self.token) {
        await fetch()
      }
      return {
        headers: {
          Authorization: `Bearer ${self.token}`,
        },
      }
    })

    return {
      attachAuthLinkToClient(client: DefaultApolloClient) {
        client.link = authLink.concat(client.link)
      },
    }
  })

async function fetchToken() {
  const { endpoint } = appConfig
  if (!endpoint) {
    throw new Error('Missing endpoint in configuration.')
  }
  const url = `${endpoint}/users/token?guest=true`
  const options = {
    headers: {
      Accept: 'application/json',
    },
  }
  const response = await fetch(url, options)
  return response.json()
}

export interface TAuthModel extends Instance<typeof AuthModel> {}
export interface TAuthModelProps extends SnapshotIn<typeof AuthModel> {}
