import { ApolloProvider } from '@speedlo/graphql'
import { updateWorker, useSentryInit } from '@speedlo/tools'
import React from 'react'
import ReactDOM from 'react-dom'

import App from './app/App'
import { Router } from './app/Router'
import { RootProvider } from './app/useRoot'
import Pacman from './common/Pacman'
import ThemeSwitchProvider from './common/ThemeSwitchProvider'
import { appConfig } from './config'
import { createApolloClient } from './graph/createClient'
import { useAnalytics } from './hooks/useAnalytics'
import { i18n, I18nManager } from './i18n'
import { createRootModel } from './models/RootModel'

const client = createApolloClient()
const root = createRootModel(client)

const Main = () => {
  useAnalytics({ reportPage: true })
  useSentryInit(appConfig.sentryDSN)
  return (
    <React.Suspense fallback={<Pacman />}>
      <RootProvider value={root}>
        <ApolloProvider client={client}>
          <I18nManager>
            <Router>
              <ThemeSwitchProvider>
                <App />
              </ThemeSwitchProvider>
            </Router>
          </I18nManager>
        </ApolloProvider>
      </RootProvider>
    </React.Suspense>
  )
}

ReactDOM.render(<Main />, document.getElementById('root'))

updateWorker.register({
  autoUpdate: true,
  onUpdate: (sw: ServiceWorker) => {
    setTimeout(() => {
      if (
        window.confirm(
          i18n.t`New version has been found. Do you want to reload application to apply it now?`,
        )
      ) {
        sw.postMessage('apply-update')
      }
    }, 5000)
  },
})

if (navigator.serviceWorker) {
  navigator.serviceWorker.addEventListener('message', event => {
    if (event.data === 'update-applied') {
      location.reload()
    }
  })
}
