import * as Hooks from '@speedlo/graphql'
import gql from 'graphql-tag'

// ====================================================
// Fragments
// ====================================================

export const GCompanyBranchFragmentDoc = gql`
  fragment GCompanyBranch on CompanyBranch {
    id
    code: wac
    brand {
      id
      name
      code: wac
    }
    company {
      id
    }
    address {
      country {
        phonePrefix
      }
    }
    phone
  }
`

export const FPriceFragmentDoc = gql`
  fragment FPrice on PriceConverted {
    value
    formattedValue
  }
`

export const GCartRecipeFragmentDoc = gql`
  fragment GCartRecipe on Recipe {
    id
    nameLabel
    descriptionLabel
    categories: sideDishesWareCategories {
      id
      nameLabel
      minCount
      maxCount
      sideDishes {
        id
        nameLabel
        price {
          ...FPrice
        }
      }
    }
  }

  ${FPriceFragmentDoc}
`

export const GCreateOrderFragmentDoc = gql`
  fragment GCreateOrder on Order {
    id
    companyBranch {
      id
    }
  }
`

export const FGeoPointFragmentDoc = gql`
  fragment FGeoPoint on GeoPoint {
    latitude
    longitude
  }
`

export const FJsonAddressFragmentDoc = gql`
  fragment FJsonAddress on JsonAddress {
    street
    houseNumber
    city
    state
  }
`

export const FAddressFragmentDoc = gql`
  fragment FAddress on Address {
    id
    location {
      ...FJsonAddress
    }
  }

  ${FJsonAddressFragmentDoc}
`

export const FCompanyBranchStatusFragmentDoc = gql`
  fragment FCompanyBranchStatus on CompanyBranch {
    id
    isOpen
    willOpenAt
    willCloseAt
    businessHours: businessHoursIntervals {
      openAt
      closeAt
      closed
      closedNote
    }
    defaultDeliveryTimeEnabled
    defaultPickupTimeEnabled
  }
`

export const GPickupBranchFragmentDoc = gql`
  fragment GPickupBranch on CompanyBranch {
    id
    name
    gps {
      ...FGeoPoint
    }
    address {
      ...FAddress
    }
    pickupEnabled
    defaultDeliveryTime
    defaultPickupTime
    ...FCompanyBranchStatus
  }

  ${FGeoPointFragmentDoc}
  ${FAddressFragmentDoc}
  ${FCompanyBranchStatusFragmentDoc}
`

export const FPromoCodeFragmentDoc = gql`
  fragment FPromoCode on PromoCode {
    id
    code
  }
`

export const FRecipeFragmentDoc = gql`
  fragment FRecipe on Recipe {
    id
    nameLabel
    sideDishCount: sideDishesWareCategoriesCount
  }
`

export const FCartItemFragmentDoc = gql`
  fragment FCartItem on OrderRecipe {
    id
    automatic
    recipe {
      ...FRecipe
    }
  }

  ${FRecipeFragmentDoc}
`

export const FOrderFragmentDoc = gql`
  fragment FOrder on Order {
    id
    countWares
    items {
      ...FCartItem
      price: priceWithSideDishes {
        ...FPrice
      }
      sideDishes {
        ...FCartItem
      }
    }
    coverPrice {
      ...FPrice
    }
    transportFee {
      ...FPrice
    }
    discounts {
      code
      description
      price {
        ...FPrice
      }
    }
    totalDiscount {
      ...FPrice
    }
    totalSum {
      ...FPrice
    }
    tip {
      ...FPrice
    }
  }

  ${FCartItemFragmentDoc}
  ${FPriceFragmentDoc}
`

export const FOrderFinishedFragmentDoc = gql`
  fragment FOrderFinished on Order {
    id
    items {
      ...FCartItem
      sideDishes {
        ...FCartItem
      }
    }
    totalSum {
      ...FPrice
    }
    orderPayments {
      paymentMethod {
        paymentType {
          enum
        }
      }
    }
  }

  ${FCartItemFragmentDoc}
  ${FPriceFragmentDoc}
`

export const FFoodMenuCategoryFragmentDoc = gql`
  fragment FFoodMenuCategory on WareCategory {
    id
    nameLabel
    pictogram
  }
`

export const FFoodMenuRecipeFragmentDoc = gql`
  fragment FFoodMenuRecipe on Recipe {
    id
    image(width: 192, height: 192)
    nameLabel
    descriptionLabel
    price {
      ...FPrice
    }
    sideDishCount: sideDishesWareCategoriesCount
  }

  ${FPriceFragmentDoc}
`

export const FCartPresenceFragmentDoc = gql`
  fragment FCartPresence on Order {
    items {
      id
      recipe {
        id
      }
    }
  }
`

export const FOrderMobileFragmentDoc = gql`
  fragment FOrderMobile on Order {
    id
    totalSum {
      ...FPrice
    }
    countWares
    ...FCartPresence
  }

  ${FPriceFragmentDoc}
  ${FCartPresenceFragmentDoc}
`

export const FShopOpeningHoursFragmentDoc = gql`
  fragment FShopOpeningHours on CompanyBranch {
    defaultDeliveryTime
    defaultPickupTime
    ...FCompanyBranchStatus
  }

  ${FCompanyBranchStatusFragmentDoc}
`

