export const GBusinessLookupDocument = gql`
  query GBusinessLookup($code: String, $url: String) {
    lookup: companyBranchLookUp(wac: $code, hostUrl: $url) {
      branch: companyBranch {
        ...GCompanyBranch
      }
      isBrand: isBrandWac
      isMobileApp
      knownBasePath
      orderOrigin
    }
  }

  ${GCompanyBranchFragmentDoc}
`

export function useGBusinessLookup(
  variables?: GBusinessLookupVariables,
  baseOptions?: Hooks.QueryHookOptions<GBusinessLookupVariables>,
) {
  return Hooks.useQuery<GBusinessLookupQuery, GBusinessLookupVariables>(
    GBusinessLookupDocument,
    variables,
    baseOptions,
  )
}

