import gql from 'graphql-tag'

import { buildQuery } from '../../graph/buildQuery'
import {
  FAddressFragmentDoc,
  FCompanyBranchStatusFragmentDoc,
  FGeoPointFragmentDoc,
} from '../../graph/generated'
import { GPickupMapBranch, GPickupMapBrand } from '../../graph/types'

const PickupBranchFragment = gql`
  fragment GPickupBranch on CompanyBranch {
    id
    name
    gps {
      ...FGeoPoint
    }
    address {
      ...FAddress
    }
    pickupEnabled
    defaultDeliveryTime
    defaultPickupTime
    ...FCompanyBranchStatus
  }
  ${FCompanyBranchStatusFragmentDoc}
  ${FGeoPointFragmentDoc}
  ${FAddressFragmentDoc}
`

export const PickupMapBrandQuery = gql`
  query GPickupMapBrand($brandId: ID!) {
    branches: companyBranches(brand: $brandId) {
      ...GPickupBranch
    }
  }
  ${PickupBranchFragment}
`

export const PickupMapBranchQuery = gql`
  query GPickupMapBranch($branchId: ID!) {
    branch: companyBranch(id: $branchId) {
      ...GPickupBranch
    }
  }
  ${PickupBranchFragment}
`

export const QueryPickupMapBrand = buildQuery<
  GPickupMapBrand.Query,
  GPickupMapBrand.Variables
>(PickupMapBrandQuery)

export const QueryPickupMapBranch = buildQuery<
  GPickupMapBranch.Query,
  GPickupMapBranch.Variables
>(PickupMapBranchQuery)
